import { mergeClassName } from './mergeClassName';

type Props = Record<string, any>;

export const mergeProps = (target: Props, source: Props): Props => {
  const result: Props = { ...target };
  for (const key in source) {
    result[key] = mergeProp(key, target[key], source[key]);
  }
  return cleanProps(result);
};


export const absorbProps = (target: Props, from: Props): Props => {
  const result: Props = { ...target };
  for (const key in result) {
    result[key] = mergeProp(key, target[key], from[key]);
  }
  return cleanProps(result);
};

const mergeProp = (key: string, target: any, source: any): any => {

  if (typeof target === 'undefined') {
    return source;
  }
  if (typeof source === 'undefined') {
    return target;
  }

  if (key === 'className' && typeof target === 'string' && typeof source === 'string') {
    return mergeClassName(target, source);
  }

  if (key === 'style' && typeof target === 'object' && typeof source === 'object') {
    return {
      ...source,
      ...target,
    };
  }
  if (typeof target === 'function' && typeof source === 'function') {
    return (...args: any) => {
      target(...args);
      source(...args);
    }
  }
  return target;
};

const cleanProps = (props: Props): Props => {

  for (const key in props) {
    if (props[key] === undefined) {
      delete props[key];
    }
  }
  return props;
};